.review-list {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px; }
  .review-list .author {
    word-wrap: break-word;
    width: 10%;
    font-size: 12px;
    font-family: "Titillium Web", sans-serif;
    color: #7d7b7a; }
  .review-list .content-wrapper {
    width: 80%;
    flex: 1;
    min-width: 0; }
    .review-list .content-wrapper .content {
      padding-left: 10px;
      line-height: 1.5;
      color: #c7c6c6;
      font-size: 12px; }
