.container {
  padding: 0 25px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .container .logo {
    display: flex;
    align-items: center;
    flex-grow: 3;
    min-width: 110px; }
    .container .logo .logo-text {
      width: 60px;
      display: inline-block;
      margin-left: 10px;
      color: white;
      font-size: 16px;
      font-family: "Titillium Web", sans-serif; }
  .container .search-container {
    position: relative;
    flex-grow: 1; }
    .container .search-container .search-input {
      width: 100%;
      background: #4b4b4b;
      color: white;
      font-family: "Roboto", sans-serif;
      font-size: 14px;
      border-radius: 3px;
      border: 0;
      outline: 0;
      padding: 10px;
      padding-right: 36px; }
    .container .search-container .button {
      height: 36px;
      width: 36px;
      background: transparent;
      border: 0;
      position: absolute;
      top: 0;
      right: 0;
      cursor: pointer; }
      .container .search-container .button .search-icon {
        font-size: 14px; }
