.loading-container {
  padding: 50px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  flex-wrap: wrap; }

.container {
  padding: 0 25px; }
  .container .background-container {
    position: fixed;
    transform: translateY(-50%);
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1; }
    .container .background-container .background {
      width: 100%;
      filter: blur(30px);
      opacity: 0.1; }
  .container .info-container {
    position: relative;
    display: flex;
    justify-content: center; }
    @media (max-width: 450px) {
      .container .info-container {
        display: block; } }
    .container .info-container .img-container {
      flex: 1;
      min-width: 30%; }
      .container .info-container .img-container img {
        width: 100%;
        box-shadow: 8px 7px 9px 0px #00000059; }
    .container .info-container .info-list {
      flex: 1;
      flex-grow: 3;
      padding-left: 20px;
      color: #c7c6c6; }
      @media (max-width: 450px) {
        .container .info-container .info-list {
          padding-left: 0;
          margin-top: 20px; } }
      .container .info-container .info-list .title-container {
        display: flex;
        align-items: center; }
        .container .info-container .info-list .title-container .title {
          font-size: 25px;
          font-family: "Titillium Web", sans-serif;
          color: white; }
          .container .info-container .info-list .title-container .title .original {
            color: #c7c6c6;
            margin-top: 8px;
            font-size: 14px;
            font-family: "Titillium Web", sans-serif; }
        .container .info-container .info-list .title-container .vote {
          position: relative;
          margin-left: 20px;
          width: 50px; }
          .container .info-container .info-list .title-container .vote span {
            color: #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); }
      .container .info-container .info-list .info {
        position: relative;
        margin-top: 30px; }
        .container .info-container .info-list .info p {
          display: flex;
          margin-top: 10px;
          font-size: 14px;
          color: #17a398; }
          .container .info-container .info-list .info p .title {
            min-width: 100px;
            font-size: 12px;
            font-family: "Titillium Web", sans-serif;
            color: #7d7b7a; }
        .container .info-container .info-list .info .overview {
          margin-bottom: 40px;
          line-height: 21px;
          color: #c7c6c6; }
        .container .info-container .info-list .info .homepage a {
          color: #17a398; }
