.movie {
  margin: 1%;
  width: 18%;
  border-radius: 3px;
  background: #fff;
  overflow: hidden;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
  font-size: 0; }
  .movie:hover img {
    transform: scale(1.2, 1.2); }
  .movie img {
    width: 100%;
    transition: all 0.4s ease-in-out;
    transform: scale(1.1, 1.1); }
  @media (max-width: 450px) {
    .movie {
      width: 100%;
      margin: 0;
      margin-bottom: 10px; } }
  @media (min-width: 450px) and (max-width: 875px) {
    .movie {
      margin: 1%;
      width: 23%; } }

.movie-img-wrap {
  position: relative;
  width: 100%;
  overflow: hidden; }

.movie-info {
  position: relative;
  padding: 1.3rem 1rem 1rem 1rem;
  min-height: 4rem; }
  .movie-info .title {
    font-size: 0.8rem;
    line-height: 1rem;
    font-family: "Titillium Web", sans-serif;
    color: black;
    text-decoration: none; }
  .movie-info .genres {
    display: block;
    margin-top: 0.5rem;
    font-size: 0.7rem;
    font-family: "Roboto", sans-serif; }
  .movie-info .avg {
    position: absolute;
    right: 0.5rem;
    top: -1.25rem;
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    background: #33312e;
    border-radius: 50%;
    font-size: 0.7rem;
    font-family: "Exo", sans-serif;
    z-index: 10; }
    .movie-info .avg span {
      color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
